import Styled from "styled-components";


export const BSKILLS = Styled.div`
padding: 50px 0;
overflow: hidden;
`

export const Brofile = Styled.div`
width: 50%;
float: left;
`

export const List = Styled.ul`
list-style: none
`

export const Item = Styled.li`
margin-bottom: 8px
`

export const Span = Styled.span`
display: inline-block;
width: 100px;
font-weight: bold
`

export const Spanweb = Styled.span`
font-weight: normal;
color: #eb5424
`

export const Skills = Styled.div`
width: 50%;
float: left;
`

export const SkillsDesc = Styled.p`
font-size: 15px;
color: #888;
line-height: 1.5;
margin-bottom: 20px
`

export const Bar = Styled.div`
overflow: hidden;
padding: 10px 0;
margin-bottom: 10px
`

export const Bartitle = Styled.span`
float: left;
`


export const Barperc = Styled.span`
float: right;
margin-right: 100px
`

export const Barent = Styled.div`
height: 2px;
clear: both;
background: #b21313;
position: relative;
top: 5px
`

export const Barentspan = Styled.div`
background: #b21313;
position: absolute;
display: block;
top: 0;
left: 0;
bottom: 0;
display:block
width: 90%;
`

export const Barentspansp1 = Styled.span`
width: 100%;
`

export const Barentspansp2 = Styled.span`
width: 90%;
`

export const Barentspansp3 = Styled.span`
width: 80%;
`

export const SkillsTitle = Styled.h2`
font-size: 40px; 
margin-bottom: 20px
` 
export const SkillsTitleSpan = Styled.span`
font-weight: normal;
` 

